<template>
    <div class="article">
        <ul>
            <li class="articlebox" v-for="(article,index) in articleShow" :key="index" @click="toArticles(article)">
                <div class="left">
                    <h1 class="tittle">{{article.title}}</h1>
                    <p class="content">{{article.description}}</p>
                    <p class="date">文章的发布时间</p>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import {getArticleTitle} from "../../../../service/api";

    export default {
        name: "Article",
        data() {
            return {
                articleShow: []
            };
        },
        created() {
            //请求网络数据
            getArticleTitle()
                .then(response => {
                    this.articleShow = response.data;
                })
                .catch(error => {
                    console.log(error);
                });
        },
        methods: {
            toArticles(article) {
                this.$router.push({
                    path: `/articles/${article.id}`
                });
            }
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../../../style/_statement.scss";

    .article {
        margin: 5% auto;
        width: 90%;
        font-size: 16px;
    }

    .articlebox {
        margin: 50px 20px;
        position: relative;
        cursor: pointer;
    }

    .left {
        display: inline-block;
    }

    .tittle {
        line-height: 30px;
        font-size: 30px;
        margin: 10px 0;
    }

    .content {
        font-size: 20px;
        line-height: 40px;
        margin: 10px 0;
    }

    .comment {
        font-size: 15px;
        line-height: 15px;
        margin: 40px 0;
    }


    @include styleOtherMPad {
        .articlebox {
            margin: 20px 0;

        }
        .article {
            margin: 0 auto;
        }
        .left {
            width: 100%;
        }
        .tittle {
            line-height: 26px;
            font-size: 16px;
            margin: conversionPX(5) auto;
        }

        .content {
            font-size: 16px;
            line-height: 20px;
            margin: 0;
        }
        .date {
            margin-top: 10px;
            font-size: 12px;
        }
        .comment {
            font-size: 12px;
            line-height: 12px;
            margin: 10px 0;
        }
    }
</style>